import { ToastService } from './../../../../_services/_toast/_toast.service';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { DbService } from '../../../../_services';
import { Constants } from '../../../../constants';
import { SearchInvoiceType } from '../../../../enums';

@Component({
  templateUrl: './view-invoices.component.html',
  styleUrls: ['./view-invoices.component.css'],
})
export class ViewInvoicesComponent implements OnInit {
  constructor(
    private formBuilder: FormBuilder,
    public constants: Constants,
    private db: DbService,
    private toastService: ToastService
  ) {}

  searchInvoiceForm: FormGroup;
  invoiceList = [];
  customerList = [];
  businessList = [];
  searchBy = SearchInvoiceType.InvoiceNumber;

  ngOnInit() {
    this.InitForm();
    this.GetInvoices();
    this.GetCustomers();
    this.GetBusinesses();
  }

  public GetTotalInvoices(invoices: any[]): number[] {
    return [invoices.length];
  }

  private InitForm() {
    this.searchInvoiceForm = this.formBuilder.group({
      invoiceNumber: [],
      invoiceType: [],
      date: [],
      customerId: [],
      businessId: [],
      paymentStatus: [],
    });
  }

  public async PaymentRecieved(invoiceId: number) {}

  get InvoiceNumber() {
    return this.searchInvoiceForm.get('invoiceNumber').value;
  }
  get InvoiceType() {
    return this.searchInvoiceForm.get('invoiceType').value;
  }
  get Date() {
    return this.searchInvoiceForm.get('date').value;
  }
  get CustomerId() {
    return this.searchInvoiceForm.get('customerId').value;
  }
  get BusinessId() {
    return this.searchInvoiceForm.get('businessId').value;
  }
  get PaymentStatus() {
    return this.searchInvoiceForm.get('paymentStatus').value;
  }

  private async GetInvoices() {
    try {
      this.invoiceList = await this.db.getInvoices();
    } catch (error) {
      this.toastService.showError(error);
    }
  }

  private async GetCustomers() {
    try {
      this.customerList = await this.db.getCustomers();
    } catch (error) {
      this.toastService.showError(error);
    }
  }

  private async GetBusinesses() {
    try {
      this.businessList = await this.db.getBusinesses();
    } catch (error) {
      this.toastService.showError(error);
    }
  }

  public async ViewInvoice(invoiceId: number) {
    try {
      const invoice = await this.db.getInvoice(invoiceId);
      console.log(
        'ðŸš€ - file: view-invoices.component.ts - line 116 - ViewInvoicesComponent - ViewInvoice - invoice',
        invoice
      );

      const uint = new Uint8Array(invoice.invoice.data);
      console.log(
        'ðŸš€ - file: view-invoices.component.ts - line 119 - ViewInvoicesComponent - ViewInvoice - uint',
        uint
      );

      const blob = new Blob([uint.buffer], { type: 'application/pdf' });
      console.log(
        'ðŸš€ - file: view-invoices.component.ts - line 119 - ViewInvoicesComponent - ViewInvoice - blob',
        blob
      );

      const url = window.URL.createObjectURL(blob);
      window.open(url, '_blank');
    } catch (error) {
      this.toastService.showError(error);
    }
  }
}
