import { Pipe, PipeTransform } from '@angular/core';
import { SearchInvoiceType } from '../../enums/enums';

@Pipe({
  name: 'searchInvoices',
})
export class SearchInvoicesPipe implements PipeTransform {
  transform(invoices: any[], searchInput: any, searchType: SearchInvoiceType, combinedType? : SearchInvoiceType): any[] {
    if (searchInput == null) {
      return [];
    }

    switch (searchType) {
      case SearchInvoiceType.InvoiceNumber:
        var results = invoices.filter(function (inv) {
          return (
            inv.invoiceNumber.toString().toLowerCase().includes(searchInput)
          );
        });
        break;
        case SearchInvoiceType.PaymentStatus:
          var results = [];
          invoices.forEach(invoice => {
            if (invoice.paid == searchInput) {
              results.push(invoice);
            }
          });
          break;
      case SearchInvoiceType.InvoiceType:
        var results = [];
        invoices.forEach(invoice => {
          if (invoice.invoiceType == searchInput) {
            results.push(invoice);
          }
        });
        break;
      case SearchInvoiceType.Date:
        var results = [];
        invoices.forEach(invoice => {
          var date = new Date(invoice.invoiceDate);
          date.setHours(0, 0, 0, 0);
          searchInput.setHours(0, 0, 0, 0);
          var invDate = date.getTime();
          var searchedDate = searchInput.getTime();
          if (invDate == searchedDate) {
            results.push(invoice);
          }
        });
        break;
      case SearchInvoiceType.Business:
        var results = [];
        invoices.forEach(invoice => {
          if (invoice.businessId == searchInput) {
            results.push(invoice);
          }
        });
        break;
      case SearchInvoiceType.Customer:
        var results = [];
        invoices.forEach(invoice => {
          if (invoice.customerId == searchInput) {
            results.push(invoice);
          }
        });
        break;
      default:
        var results = [];
        break;
    }
    return results;
  }
}
